.navbar-container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 24px 0 12px 0;
  position: relative;
  z-index: 10;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.navbar {
  width: fit-content;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 100px;
  border: 1px solid rgba(221, 229, 237, 1);
  box-shadow: rgba(0, 181, 100, 0.08) 0px 22px 70px 0px;
  backdrop-filter: blur(5px);
  padding: 12px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeInDown 0.6s ease-out;
  opacity: 0;
  animation-fill-mode: forwards;
}

.navbar-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.nav-link {
  padding: 8px 16px;
  text-decoration: none;
  color: #333333;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 16px;
  font-weight: 500;
  border-radius: 100px;
  transition: background-color 0.2s ease;
  white-space: nowrap;
  line-height: 1.5;
}

.nav-link:hover {
  background-color: rgba(244, 247, 249, 0.5);
}

.nav-logo {
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  flex-shrink: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 36px;
  font-weight: 900;
  color: #00B564;
  white-space: nowrap;
  line-height: 1.2;
  letter-spacing: -0.02em;
}

@keyframes fadeInDown {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .navbar {
    padding: 10px 16px;
  }
  
  .nav-link {
    padding: 6px 12px;
    font-size: 14px;
  }
  
  .nav-logo {
    width: 100px;
    padding: 6px 12px;
  }
}

